    &-slider {
        &-images {
            width: 100%;
            max-width: 430px;
            margin-bottom: 30px;
            &-dots {
                width: 100%;
                max-width: 430px;
                padding: 0 25px;
                @media (max-width: 992px) {padding: 0 20px;}
                & .owl-item{
                    @media (max-width: 992px) {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    &.active {
                        & .card-slider-dots-img {border:  1px solid #66C58F !important; }
                    }
                }

                & .owl-nav {
                    & button {
                        position: absolute;
                        top: calc(50% - 7px);
                        display: block;
                        width: 14px;
                        height: 14px;
                        background-image: url('../images/slider-arrow.svg') !important;
                        background-repeat: no-repeat !important;
                        background-position: center !important;
                        background-size: contain !important;
                        &.owl-prev {
                            left: 0;
                            transform: rotate(-90deg);
                        }
                        &.owl-next {
                            right: 0;
                            transform: rotate(90deg);
                        }
                        & span {display: none;}
                    }
                }
            }
        }
        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: 380px;
            width: 100%;
            background-color: #84cd7e;
            @media (max-width: 767px) {min-height: 250px;}
            & img {
                width: auto !important;
                max-width: 100%;
                max-height: 100%;
            }
        } 
        &-dots {
            &-img {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                max-width: 84px;
                height: 104px;
                padding: 3px;
                border: 1px solid #B7BDC2 !important;
                border-radius: 6px;
                transition: 0.3s ease;
                &:hover {border:  1px solid #66C58F !important; }
            }
        }
    }
    &-content {
        display: flex;
        align-content: flex-start;
        justify-content: space-between;
        margin-bottom: 50px;
        @media (max-width: 992px) { 
            flex-direction: column;
            align-items: center;
        }
        &-images {
            @media (max-width: 992px) { 
                margin-bottom: 50px;
                max-width: 100%;
            }
        }
        &-info {
            width: 100%;
            max-width: 450px;
            @media (max-width: 992px) { 
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                max-width:315px;
            }
            &-top {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                max-width: 315px;
                margin-bottom: 30px;
                & .btn {
                    width: 100%;
                    max-width: 100%;
                    & span {
                        font-size: 18px;
                        line-height: 18px;
                        @media (max-width: 992px) {
                            font-size: 16px;
                            line-height: 16px;
                        }
                    }
                    & img {
                        max-width: 38px;
                        margin: 0 0 6px 10px;
                    }
                    &:not(:last-child) {margin-bottom: 20px;}
                }
                & .cart-small-row {display: none;}
            }
            &-price {
                &-last {
                    position: relative;
                    display: inline-block;
                    margin: 0 auto;
                    font-family: 'Montserrat Regular';
                    font-size: 30.61px;
                    line-height: 37px;
                    text-align: center;
                    color: #696969;
                    &::before {
                        position: absolute;
                        top: calc(50% - 1px);
                        left: 0;
                        content: '';
                        display: block;
                        width: 100%;
                        height: 1px;
                        background-color: #F15C5C;
                        transform: rotate(-6.52deg);
                    }
                }
                &-new {
                    display: block;
                    margin-bottom: 28px;
                    font-family: 'Montserrat Bold';
                    font-size: 53.6054px;
                    line-height: 65px;
                    text-align: center;
                    color: #222222;
                    @media (max-width: 992px) {
                        margin-bottom: 15px;
                        font-size: 40px;
                        line-height: 50px;
                    }
                }
            }
            &-col { width: 100%;}
            &-row {
                position: relative;
                padding-left: 20px;
                &::before {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: 0;
                    display: block;
                    min-width: 8px;
                    width: 8px;
                    height: 8px;
                    background: $linear;
                    box-shadow: 0px 1.65px 4.8px rgba(0, 0, 0, 0.16);
                    border-radius: 50%;
                }
                &:not(:last-child) {margin-bottom: 18px;}
                & p {
                    font-family: 'Montserrat Regular';
                    font-size: 18px;
                    line-height: 18px;
                    color: $color-text-gray;
                    @media (max-width: 992px) {
                        font-size: 14px;
                        line-height: 14px;
                    }
                    & span {font-family: 'Montserrat Bold'; }
                }
            }
        }
    }
}


.tabs {
    &-block {
        width: 100%;
        margin-bottom: 50px;
        box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
        border-radius: 0 0 10px 10px;
        // overflow: hidden;
        @media (max-width: 992px) { margin-bottom: 30px;}
    }
    &-section {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 767px) {
            flex-direction: column;
            align-items: center;
        }
        & a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: calc(100% / 4);
            min-height: 56px;
            font-family: 'Montserrat Bold';
            font-size: 16px;
            line-height: 16px;
            text-transform: uppercase;
            background-color: #FAFAFA;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                font-size: 13px;
                line-height: 13px;
            }
            @media (max-width: 767px) {
                width: 100%;
                max-width: 100%;
            }
            &:not(:last-child) {
                border-right: 1px solid $color-gray2;
                @media (max-width: 767px) {
                    border-right: none;
                    border-bottom: 1px solid $color-gray2;
                }
            }
            & span {color: $color-text-green;}
            &::before {
                content: '';
                position: absolute;
                bottom: 100%;
                left: 0;
                display: block;
                width: 100%;
                height: 8px;
                background: $linear;
                border-radius: 4px 4px 0 0;
                transition: 0.3s ease;
                opacity: 0;
                @media (max-width: 767px) {display: none;}
            }
            &:hover {color: $color-text-green;}
            &.active {
                background-color: $color-white;
                @media (max-width: 767px) {
                    background-color: $color-text-green;
                    color: $color-white;
                }
                &:before {opacity: 1;}
            }
        }
    }
    &-content {
        padding: 45px;
        background-color: #ffffff;
        border-radius: 0 0 10px 10px;
        @media (max-width: 992px) {padding: 20px 15px;}
        & .rev-block {
            margin: 0;
            width: 100%;
            padding: 0;
            box-shadow: none;
        }
    }
}


.tab-cnt {display: none;}

.tab-cnt.active {display: block;}