.banner{
    padding: 110px 0;
    background-position: center;
    &-main {
        background: #F6F6F6;
        &-carousel {
            width: 100%;
            & .owl-dots {
                position: absolute;
                bottom: 14px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        &-item {
            position: relative;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            padding-top: 41px;
            padding-bottom: 136px;
            width: 100%;
            @media (max-width:767px) {
                flex-direction: column;
                align-items: center;
                padding-top: 25px;
                padding-bottom: 0;
            }
            @media (max-width:500px) {
                padding-bottom: 70px;
            }
            &-cnt {
                position: relative;
                z-index: 2;
            }
            &-title {
                width: 100%;
                max-width: 645px;
                padding-left: 3px;
                margin-bottom: 48px;
                font-family: 'Edo SZ';
                font-size: 51px;
                line-height: 56px;
                color: #2C8A52;
                @media (max-width:992px) {
                    font-size: 31px;
                    line-height: 36px;
                    margin-bottom: 30px;
                }
                @media (max-width:767px) {
                    max-width: 100%;
                    text-align: center;
                }
                & span {
                    display: block;
                    color: $color-orange;
                }
            }
            & .btn {
                max-width: 190px;
                box-shadow: none;
                @media (max-width:767px)  {margin: 0 auto 35px;}
                @media (max-width:500px) {margin: 0 auto 0;}
            }
            &-img {
                position: absolute;
                bottom: 0;
                right: 0;
                z-index: 1;
                @media (max-width: 1100px) {
                    max-width: 650px;
                }
                @media (max-width:900px) {
                    max-width: 550px;
                }
                @media (max-width:767px) {
                    position: static;
                    margin: 0 auto;
                }
                @media (max-width:500px) {
                    display: none;
                }
            }
        }
    }
}

.breadcrumbs {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    @media (max-width:767px) {
        flex-flow: row wrap;
    }  
    & a,
    & span {
        display: inline-block;
        font-family: 'Montserrat Bold';
        font-size: 18px;
        line-height: 18px;
        color: #FFFFFF;
        @media (max-width: 767px) {
            margin: 5px 0;
            font-size: 15px;
            line-height: 15px;
        }
    }
    & a:hover {
        @media (min-width: 1100px) {
            color: $color-green;
        }
    }
    & img {
        width: 13px;
        margin: 0 10px;
    }
    &-simple {
        margin-bottom: 38px;
        & a,
        & span {
            font-size: 15px;
            line-height: 15px;
            text-transform: capitalize;
            color: $color-gray;
        }
    }
}