.news {
    padding: 70px 0 55px;
    @media (max-width: 1100px) {
        padding: 40px 0;
    }
    &-preview {
        padding: 70px 0 85px;
        @media (max-width: 1100px) {
            padding: 40px 0;
        }
        &-list {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            flex-flow: row wrap;
            @media (max-width: 1100px) {
                flex-direction: column;
                align-items: center;
            }
        }
    }
    & .wrapper {
        display: flex;
        align-content: flex-start;
        justify-content: space-between;
        @media (max-width: 767px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &-tags {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-flow: row wrap;
        & a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 31px;
            margin-bottom: 13px;
            padding: 15px;
            font-family: 'Montserrat Regular';
            font-size: 14px;
            line-height: 14px;
            text-align: center;
            background: $linear;
            color: $color-text-gray;
            border-radius: 4px;
            transition: 0.3s ease;
            & span {
                position: relative;
                z-index: 2;
            }
            &::before {
                content: '';
                position: absolute;
                top: 1px;
                left: 1px;
                z-index: 1;
                display: block;
                width: calc(100% - 2px);
                height: calc(100% - 2px);
                background: $color-white;
                border-radius: 3px;
                transition: 0.3s ease;
            }
            &:hover {
                color: $color-white;
                &::before {opacity: 0;}
            }
            &:not(:last-child) {margin-right: 13px;}
        }
    }
    &-articles,
    & .article-block-wrapper {
        width: 100%;
        max-width: 825px;
        @media (max-width: 767px) {
            max-width: 100%;
            margin-bottom: 50px;
        }
    }
    &-article {
        display: block;
        width: 100%;
        margin-bottom: 40px;
        border: 1px solid #E4DFDF;
        border-radius: 5.86px;
        overflow: hidden;
        &-img {
            display: block;
            width: 100%;
            & img {width: 100%;}
        }
        &-cnt {
            padding: 55px;
            @media (max-width: 1050px) {padding: 30px 15px;}
        }
        &-title {
            display: block;
            width: 100%;
            margin-bottom: 15px;
            font-family: 'Montserrat Bold';
            font-size: 25px;
            line-height: 30px;
            text-align: center;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 22px;
            }
            &:hover {color: $color-text-green;}
        }
        &-date {
            position: relative;
            margin-bottom: 38px;
            font-family: 'Montserrat Medium';
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #696969;
            &::before {
                content: "";
                position: absolute;
                top: calc(100% + 20px);
                left: calc(50% - 25px);
                display: block;
                width: 50px;
                height: 1px;
                background-color: $color-text-green;
            }
        }
        & p {
            margin-bottom: 32px;
            font-family: 'Montserrat Regular';
            font-size: 18px;
            line-height: 25px;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
        & .btn {
            max-width: 155px;
            margin: 0 auto 25px;
        }
        &-info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: 500px) {
                flex-direction: column;
                align-items: center;
            }
            &-tag {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                @media (max-width: 500px) {
                    margin-bottom: 30px;
                }
                & img {
                    max-width: 22px;
                    width: 22px;
                    min-width: 22px;
                    margin-right: 10px;
                }
                & span {
                    font-family: 'Montserrat Medium';
                    font-size: 14px;
                    line-height: 14px;
                    color: #696969;
                    transition: 0.3s ease;
                }
                &:hover span {color: $color-text-green;}
            }
        }
        &-share {
            display: flex;
            align-items: center;
            justify-content: center;
            & img {
                max-width: 22px;
                width: 22px;
                min-width: 22px;
                margin-right: 10px;
            }
            & p {
                margin: 0 20px 0 0;
                font-family: 'Montserrat Medium';
                font-size: 14px;
                line-height: 14px;
                color: $color-text-green;
            }
            & a {
                display: flex;
                align-items: center;
                justify-content: center;
                max-width: 30px;
                transition: 0.3s ease;
                &:hover {opacity: 0.7;}
                & img {max-width: 100%;}
                &:not(:last-child) {margin-right: 8px;}
            }
        }
    }
    &-sidebar {
        width: 100%;
        max-width: 270px;
        padding-left: 10px;
        @media (max-width: 767px) {
            max-width: 100%;
            padding-left: 0;
        }
    }
    &-popular {
        &-list {padding-bottom: 15px;}
        &-item {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            padding-bottom: 12px;
            border-bottom:  1px dashed $color-gray2;
            &:not(:last-child) {margin-bottom: 12px;}
            &-img {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 83px;
                min-width: 83px;
                height: 83px;
                margin-right: 10px;
                border: 1px solid #E4DFDF;
                box-sizing: border-box;
                border-radius: 5.86px;
                & img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
            &-cnt {
                width: 100%;
                & a,
                & span {
                    font-family: 'Montserrat Medium';
                    font-size: 14px;
                    line-height: 17px;
                    color: $color-text-gray;
                    transition: 0.3s ease;
                }
                & a {
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    overflow: hidden;
                    -webkit-box-orient: vertical;
                    width: 100%;
                    height: 68px;
                    margin-bottom: 4px;
                    &:hover {color: $color-text-green;}
                }
                & span {
                    display: block;
                    color:#696969;
                }
            }
        }
    }
}

.article {
    &-prev {
        position: relative;
        width: 100%;
        max-width: 255px;
        min-height: 256px;
        margin-bottom: 30px;
        padding: 39px 10px 10px 25px;
        border-radius: 7px;
        overflow: hidden;
        @media (max-width: 550px) {
            max-width: 100%;
            min-height: 250px;
            padding: 30px 10px 30px 15px;
        }
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.26);
            transition: 0.3s ease;
        }
        &-big {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-end;
            width: 100%;
            max-width: 540px;
            min-height: 542px;
            padding: 15px 30px 75px 65px;
            transition: 0.3s ease;
            @media (max-width: 550px) {
                max-width: 100%;
                min-height: 250px;
                padding: 30px 10px 30px 15px;
            }
        }
        &-cnt {
            position: relative;
            z-index: 2;
            display: block;
            & span {display: block;}
        }
        &-category {
            margin-bottom: 21px;
            font-family: 'Montserrat Bold';
            font-size: 16px;
            line-height: 20px;
            letter-spacing: 0.105em;
            color: #2C8A52;
            background: $linear;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        &-title {
            font-family: 'Montserrat Regular';
            font-size: 25px;
            line-height: 30px;
            color: $color-white;
        }
        &-block {
            display: flex;
            justify-content: space-between;
            flex-flow: row wrap;
            width: 100%;
            max-width: 540px; 
        }
        &-small {
            transition: 0.3s ease;
            @media (min-width: 1100px) {
                &:not(:hover) {
                    & .article-prev-title {color: $color-text-gray;}
                    &::before {background: #F9F9F9;}
                }
            }
        }
        &-more{
            display: flex;
            align-items: center;
            justify-content: center;
            @media (max-width: 550px) {
                max-width: 100%;
                min-height: 0;
                padding: 0;
            }
            &::before {display: none;}
        }
    }
    &-block {
        width: 100%;
        margin-bottom: 20px;
        &-wrapper {
            & .news-article-info {margin-bottom: 45px;}
        }
        &-more {
            width: 100%;
            max-width: 315px;
            & a {
                display: block;
                font-family: 'Montserrat Medium';
                font-size: 18px;
                line-height: 22px;
                text-decoration: underline;
                color: $color-text-gray;
                transition: 0.3s ease;
                @media (max-width: 992px) {
                    font-size: 16px;
                    line-height: 20px;
                }
                &:not(:last-child) {margin-bottom: 18px;}
                &:hover {color: $color-text-green;}
            }
        }
    }
    &-head {
        & h1 {
            display: block;
            width: 100%;
            margin-bottom: 12px;
            font-family: 'Montserrat Medium';
            font-size: 25px;
            line-height: 30px;
            text-align: center;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                font-size: 22px;
                line-height: 26px;
            }
        }
    }
    &-date {
        margin-bottom: 28px;
        font-family: 'Montserrat Medium';
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: $color-text-green;
    }
}

.article-cnt,
.section-info {
    & img {
        max-width: 100%;
        &:first-child {
            width: 100%;
            margin-bottom: 52px;
        }
    }
    h2 {
        font-family: 'Montserrat Bold';
        font-size: 40px;
        line-height: 50px;
        text-transform: uppercase;
        color: $color-text-gray; 
        @media (max-width: 992px) {
            font-size: 20px;
            line-height: 30px;
        }
    }
    & a {
        font-family: 'Montserrat Bold';
        font-size: 18px;
        line-height: 25px;
        text-decoration: underline;
        color: #2D9CDB;
        &:hover {text-decoration: none;}
        @media (max-width: 992px) {
            font-size: 16px;
            line-height: 20px;
        }
    }
    & p {
        font-family: 'Montserrat Regular';
        font-size: 18px;
        line-height: 25px;
        color: $color-text-gray;
        @media (max-width: 992px) {
            font-size: 16px;
            line-height: 22px;
        }
        & b {font-family: 'Montserrat Bold';}
    }
    & div {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        & img {margin-bottom: 5px;}
        & p {
            font-family: 'Montserrat Light';
            font-size: 14px;
            line-height: 20px;
            color: #696969;
        }
    }
    & ul {
        list-style: none;
        padding: 0;
        & li {
            position: relative;
            padding-left: 30px;
            font-family: 'Montserrat Regular';
            font-size: 18px;
            line-height: 30px;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 25px;
            }
            &::before {
                content: '';
                position: absolute;
                top: 8px;
                left: 0;
                display: block;
                width: 16px;
                height: 12px;
                background-image: url('../images/check-true2.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}