.checkout {
    & form {width: 100%;}
    &-list {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width: 992px) {flex-direction: column;}
    }
    &-fields {
        width: 100%;
        max-width: 540px;
        margin-right: 15px;
        @media (max-width: 992px) {
            max-width: 100%;
            margin-right: 0;
            margin-bottom: 30px;
        }
        & .form-field,
        & .form-row {max-width: 100%;}
    }
    &-info {
        width: 100%;
        max-width: 475px;
        padding:  23px 30px 45px;
        background: #FFFFFF;
        border: 1px solid #66C58F;
        border-radius: 4px;
        @media (max-width: 992px) {
            max-width: 100%;
            padding:  0;
            background: transparent;
            border: none;
            border-radius: 0;
        }
        &-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 17px 0;
            border-bottom: 1px solid #B7BDC3;
            &-title {
                & p {
                    font-family: 'Montserrat Bold';
                    font-size: 18px;
                    line-height: 22px;
                    color: $color-text-gray;
                    &:last-child {
                        text-align: right;
                        @media (max-width: 500px)  {display: none;}
                    }
                }
            }
            &-items {
                @media (max-width: 500px) {
                    flex-flow: row wrap;
                    justify-content: space-between;
                    align-items: center;
                }
                & a,
                & span,
                & p {
                    display: inline-block;
                    font-family: 'Montserrat Regular';
                    font-size: 15px;
                    line-height: 24px;
                    color: $color-text-lightgray;
                    transition: 0.3s ease;
                    @media (max-width: 500px) {display: block;}
                }
                & a {
                    min-width: 213px;
                    max-width: 213px;
                    @media (max-width: 500px)  {
                        margin-bottom: 5px;
                        min-width: 0;
                        width: 100%;
                        max-width: calc(100% - 45px);
                    }
                    &:hover {color: $color-text-green;}
                }
                & > span {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    min-width: 35px;
                    max-width: 35px;
                    @media (max-width: 500px)  {margin-bottom: 5px; }
                    & img {max-width: 17px;}
                }
                & p {
                    min-width: 130px;
                    max-width: 130px;
                    text-align: right;
                    @media (max-width: 500px)  {
                        min-width: 0;
                        max-width: 100%;
                        width: 100%;
                        text-align: left;
                    }
                    & span {
                        display: inline-block;
                        margin-left: 5px;
                    }
                }
            }
            &-price {
                margin-bottom: 32px;
                padding: 25px 0;
                & p {
                    font-family: 'Montserrat Bold';
                    font-size: 18px;
                    line-height: 22px;
                    color: $color-text-gray;
                    @media (max-width: 992px) {
                        font-size: 16px;
                        line-height: 18px;
                    }
                    & span {
                        font-size: 25px;
                        line-height: 30px;
                        color: $color-text-green;
                        @media (max-width: 992px) {
                            font-size: 20px;
                            line-height: 22px;
                        }
                    }
                }
            }
            &-method {
                margin-bottom: 40px;
                & .custom-select {
                    width: 100%;
                    max-width: 100%;
                }
                & p {
                    margin-bottom: 17px;
                    font-family: 'Montserrat Bold';
                    font-size: 18px;
                    line-height: 22px;
                    color: $color-text-gray;
                    @media (max-width: 767px) {
                        font-size: 14px;
                        line-height: 14px;
                    }
                }
            }
        }
        & .btn {
            width: 100%;
            max-width: 100%;
        }
    }
}