.header {
    background: $color-white;
    box-shadow: 0px 4px 29px rgba(0, 0, 0, 0.07);
    &.open {

        // & .header-bottom {display: block;}

        .menu-trigger span {
            position: relative;
            width: 100%;
        }

        .menu-trigger  {justify-content: center;}

        .menu-trigger span:nth-child(1) {
            transform: rotate(-40deg);
            margin-bottom: -3px;
        }

        .menu-trigger span:nth-child(2) {display: none;}

        .menu-trigger span:nth-child(3) {transform: rotate(40deg);}
    }
    &.fixed {
        @media (min-width: 993px){
            box-shadow: none;
            & .header-bottom {
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                z-index: 10;
                background-color: $color-white;
                box-shadow: 0px 4px 29px rgba(0, 0, 0, 0.07);
            }
        }
    }
    &-top {
        & .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        padding: 29px 0;
        background-color: $color-white;
        border-bottom: 1px solid $color-gray2;
        &-right {
            display: flex;
            align-items: center;
            justify-content: center;
            & .menu-trigger {
                margin-left: 50px;
                @media (max-width:600px) {margin-left: 25px;}
            }
        }
        & .menu-trigger {display: none;}
        @media (max-width: 992px) {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            z-index: 16;
            padding: 12px 0;
            background-color: $color-white;
            & .menu-trigger {display: flex;}
            & .header-phones,
            & .search-form,
            & .callback-btn {display: none;}
        }
    }
    &-bottom {
        padding: 9px 0 15px;
        @media (max-width: 992px)  {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            z-index: 15;
            display: none;
            padding: 100px 0 100px;
            overflow-y: scroll;
            background-color: $color-white;
        }
        &-content {
            display: none;
            &-2 {margin-bottom: 0;}
            @media (max-width: 992px) {
                display: flex;
                align-items:center;
                justify-content: flex-start;
                flex-direction: column;
                width: 100%;
                margin-bottom: 30px;
                & .search-form {
                    max-width: 500px;
                    margin: 0 auto;
                }
                & .header-phones {
                    width: 100%;
                    margin: 30px 0;
                }
            }
        }
    }
    &-phones {
        @media (max-width: 992px) {
            display: flex;
            align-items:center;
            justify-content: flex-start;
            flex-direction: column;
        }
        & a {
            display: block;
            font-family: 'Montserrat Bold';
            font-size: 14px;
            line-height: 14px;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 18px;
                text-transform: uppercase;
            }
            &:hover {
                @media (min-width: 1100px) {
                    color: $color-green;
                }
            }
            &:first-child {
                margin-bottom: 6px;
                @media (max-width: 992px) {
                    margin-bottom: 15px;
                }
            }
        }
    }
    & nav {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        @media (max-width: 992px) {
            flex-direction: column;
            align-items: center;
        }
        & a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            font-family: 'Montserrat Bold';
            font-size: 14px;
            line-height: 14px;
            min-height: 20px;
            color: $color-text-gray;
            @media (max-width: 992px) {
                min-height: 0;
                font-size: 18px;
                line-height: 18px;
                text-transform: uppercase;
            }
            &::before {
                content: '';
                position: absolute;
                top: calc(100% + 12px);
                display: block;
                width: 100%;
                height: 3px;
                background: $linear;
                display: none;
            }
            & img {
                margin-right: 6px;
            }
            &:not(:last-child) {
                margin-right: 39px;
                @media (max-width: 992px) {margin: 0 0 20px;}
            }
            &:hover {
                @media (min-width: 1100px) {
                    color: $color-green;
                    background: $linear;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    &::before {display: block;}
                }
            }
            &.active {
                color: $color-green;
                background: $linear;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                @media (min-width: 993px) {
                    &::before {display: block;}
                }
            }
        }
    }
}

.logo {
    display: block;
    font-family: 'Montserrat Bold';
    font-size: 25px;
    line-height: 25px;
    color: $color-green;
    background: $linear;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
    @media (max-width: 992px)  {
        font-size: 16px;
        line-height: 16px;
    }
}

.search-form {
    width: 100%;
    max-width: 407px;
    padding: 4px 4px 4px 13px;
    border: 1px solid $color-gray2;
    border-radius: 4px;
    background-color: $color-white;
    @media (max-width: 1100px) {
        max-width: 330px;
    }
    & form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        background-color: $color-white;
        & input {
            display: inline-block;
            width: calc(100% - 100px);
            font-family: 'Montserrat Medium';
            font-size: 14px;
            line-height: 14px;
            color: $color-gray;
            outline: none;
            border: none;
            &::-webkit-input-placeholder {color: $color-gray;}
            &::-moz-placeholder {color: $color-gray;}
            &:-ms-input-placeholder {color: $color-gray;}
            &:-moz-placeholder {color: $color-gray;}
        }
    }
}

.menu-trigger {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-direction: column;
    width: 45px;
    height: 25px;
    transition: 0.6s ease;
    & span {
        display: block;
        width: 100%;
        height: 3px;
        background: $linear;
        transition: 0.2s cubic-bezier(0, 0, 0.2, 1);
        &:nth-child(1) {width: 75%;}
        &:nth-child(3) {width: 40%;}
    }
}
