.contacts {
    &-main {
        padding: 70px 0 85px;
        @media (max-width: 1100px) {
            padding: 40px 0;
        }
        &-list {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            @media (max-width: 1100px) {
                padding: 40px 0;
            }
            @media (max-width: 992px) {
                flex-direction: column;
                align-items: center;
            }
            & form {
                width: 100%;
                max-width: 545px;
                margin-right: 15px;
                @media (max-width: 992px) {
                    max-width: 100%;
                    margin: 0 ;
                    order: 2;
                }
            }
        }
        &-info {
            width: 100%;
            max-width: 540px;
            @media (max-width: 992px) {
                max-width: 100%;
                margin: 0 0 40px;
                order: 1;
            }
            & iframe {
                width: 100%;
                margin-bottom: 25px;
            }
            &-row {
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                @media (max-width: 450px) {
                    flex-direction: column;
                }
                &:not(:last-child) {margin-bottom: 13px;}
                & p {
                    font-family: 'Montserrat Regular';
                    font-size: 16px;
                    line-height: 20px;
                    color: $color-text-gray;
                    &:not(:last-child) {margin-bottom: 13px;}
                    & b {font-family: 'Montserrat Bold';}
                    & a {
                        color: #2C8A52;
                        text-decoration: underline;
                        background: $linear;
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        &:hover {text-decoration: none;}
                    }
                }
                & div {
                    display: block;
                    padding-left: 5px;
                    & a {
                        display: block;
                        font-family: 'Montserrat Medium';
                        font-size: 22px;
                        line-height: 22px;
                        color: $color-text-gray;  
                        transition: 0.3s ease;
                        &:hover {
                            @media (min-width: 1100px) {
                                color: $color-green;
                            }
                        }
                        &:not(:last-child) {margin-bottom: 7px;}
                        @media (max-width: 767px) {
                            font-size: 16px;
                            line-height: 16px;
                        }
                    }
                }
            }
        }
        & .contacts-form-title {
            margin-bottom: 27px;
            font-family: 'Montserrat Bold';
            font-size: 28px;
            line-height: 33px;
            color: #2C8A52;
            background: $linear;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 23px;
            }
        }
        &-form {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            flex-flow: row wrap;
            @media (max-width: 992px) {
                justify-content: space-around;
            }
            & .polytic {width: 100%;}
            & input.form-field-gray {
                max-width: 243px;
                @media (max-width: 1050px) {max-width: 100%; }
            }
        }
    }
    &-info {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width: 992px) {
            flex-direction: column;
            align-items: center;
            margin-bottom: 30px;
        }
        &-cnt {
            width: 100%;
            max-width: 440px;
            margin-right: 10px;
            @media (max-width: 992px) {
                max-width: 100%;
                margin: 0 0 30px;
            }
        }
        & iframe {
            width: 100%;
            max-width: 635px;
        }
    }
    & .title {text-align: left;}
    &-requisites {
        width: 100%;
        padding: 42px 15px 48px 63px;
        background: #FFFFFF;
        box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
        border-radius: 10px;
        @media (max-width: 992px) {
            padding: 0;
            box-shadow: none;
        }
        &-title {
            margin-bottom: 23px;
            font-family: 'Montserrat Bold';
            font-size: 24px;
            line-height: 36px;
            color: $color-text-green;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 26px;
            }
        }
        &-row {
            &:not(:last-child) {margin-bottom: 35px;}
        }
        &-col {
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            @media (max-width: 992px) {
                flex-direction: column;
                align-items: flex-start;
            }
            &:not(:last-child) {
                margin-bottom: 15px;
                @media (max-width: 992px) {
                    padding-bottom: 15px;
                    border-bottom: 1px solid $color-text-gray;
                }
            }
            & span {
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 27px;
                color: $color-text-gray;
                @media (max-width: 992px) {
                    font-size: 16px;
                    line-height: 24px;
                }
                &.contacts-requisites-col-text {font-family: 'Montserrat Bold';}
            }
            & > div {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                width: 100%;
                max-width: 405px;
                margin-right: 15px;
                @media (max-width: 992px) {
                    max-width: 100%;
                    margin-right: 0;
                }
                & .contacts-requisites-col-title {
                    white-space: nowrap;
                    @media (max-width: 992px) {
                        white-space: wrap;
                        margin-bottom: 10px;
                    }
                }
                & span:not(.contacts-requisites-col-title) {
                    width: 100%;
                    height: 1px;
                    margin: 0 0 0 10px;
                    border-bottom: 1px dashed $color-text-gray ;
                    @media (max-width: 992px) {
                        display: none;
                    }
                }
            }
        }
    }
}