.catalog {
    &-prev {
        display: flex;
        justify-content: space-between;
        flex-flow: row wrap;
        @media (max-width: 1100px) {
            justify-content: space-around;
        }
        &-wrap {
            padding: 70px 0 40px;
        }
        &-item {
            display: block;
            width: 100%;
            max-width: 255px;
            margin-bottom: 30px;
            padding: 10px 5px 20px;
            background: #F9F9F9;
            border-radius: 5.86px;
            border: 2px solid #F9F9F9;
            transition: 0.3s ease;
            @media (max-width: 1100px) {
                margin: 0 5px 30px;
            }
            &.none {
                & .catalog-prev-item-img {filter: grayscale(100%);}
                & .catalog-prev-item-name {color: #C5C5C5;}
            }
            &:hover {
                @media (min-width: 1100px) {
                    border: 2px solid $color-text-green;
                    box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
                }
            }
            &-img {
                display: flex;
                align-items: flex-start;
                justify-content: center;
                width: 100%;
                height: 215px;
                margin-bottom: 20px;
                & img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
            &-name {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                overflow: hidden;
                -webkit-box-orient: vertical;
                height: 40px;
                font-family: 'Montserrat Regular';
                font-size: 16px;
                line-height: 20px;
                text-align: center;
                text-decoration: underline; 
                color: #696969;
            }
            &-more {
                border: none !important;
                background: transparent;
                display: flex;
                align-items: center;
                justify-content: center;
                box-shadow: none !important;
            }
        }
    }
}