.product {
    padding: 30px 0 85px;
    @media (max-width: 992px) {padding: 30px 0;}
    &-filter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1100px) {flex-direction: column;}
        &-sorting,
        &-checkers {
            display: flex;
            align-items: center;
            justify-content: flex-start;
        }
        &-sorting {
            width: 100%;
            max-width: 380px;
            @media (max-width: 1100px) {
                justify-content: center;
                max-width: 100%;
                margin-bottom: 30px;
            }
            @media (max-width: 500px) {
                flex-direction: column;
                p {margin: 0 0 15px;}
            }
        }
        &-checkers {
            @media (max-width: 1100px) {flex-direction: column;}
            & .btn {
                margin: 0 38px 0 17px;
                padding: 0 21px;
                @media (max-width: 1100px) {  margin: 0 0 20px;}
            }
            & .checkbox-item {margin-bottom: 0;}
        }
        &-price {
            display: flex;
            align-items: center;
            justify-content: center;
            @media (max-width: 1100px) { 
                max-width: 100%;
                margin: 0 0 20px;
            }
            @media (max-width: 500px) {
                flex-direction: column;
                p {margin: 0 0 15px;}
            }
            & div {
                display: flex;
                align-items: center;
                justify-content: center;
                &:last-child {
                    margin-left: 30px;
                    @media (max-width: 500px) {
                        margin: 5px 0 0;
                    }
                }
                & p {margin: 0;}
            }
        }
        &-wrap {
            padding: 33px 0 14px;
            border-bottom: 1px solid $color-gray2;
        }
        & p {
            font-family: 'Montserrat Regular';
            font-size: 16px;
            line-height: 16px;
            color: #696969;
            @media (max-width: 1100px) {
                font-size: 14px;
                line-height: 14px;
            }
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
    }
    &-list {
        display: flex;
        justify-content: space-between;
        flex-flow: row wrap;
        width: 100%;
        padding-bottom: 40px;
        @media (max-width: 1110px) {
            justify-content: space-around;
        }
        @media (max-width: 1073px) {
            justify-content: flex-start;
        }
        @media (max-width: 854px) {
            justify-content: space-around;
        }
    }
    &-item {
        position: relative;
        width: 100%;
        max-width: 255px;
        margin-bottom: 30px;
        padding: 25px 0 20px;
        background: #FFFFFF;
        border: 1px solid #E4DFDF;
        border-radius: 5.86px;
        transition: 0.3s ease;
        @media (max-width: 1110px) {
            margin: 0 3px 30px;
        }
        @media (max-width: 1073px) {
            margin: 0 20px 30px 0;
        }
        @media (max-width: 854px) {
            margin: 0 10px 30px;
        }
        @media (max-width: 500px) {
            margin: 0 0 30px;
        }
        @media (min-width: 1100px) {
            &:hover {
                border: 1px solid $color-text-green; 
            }
        }
        & .cart-small-row {display: none;}
        &-category {
            position: absolute;
            top: 14px;
            left: 14px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            width: calc(100% - 28px);
            & span {
                display: inline-block;
                min-height: 23px;
                padding: 3px 8px;
                border-radius: 4px;
                font-family: 'Montserrat Bold';
                font-size: 14px;
                line-height: 14px;
                color: #FFFFFF;
                &:not(:last-child) {margin-bottom: 7px;}
                &.month {background-color: $color-text-green;}
                &.new {background-color: #3768B3;}
                &.sale {background-color: #F15C5C;}
            }
        }
        &-cnt {
            width: 100%;
            padding: 0 11px;
        }
        &-title {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            height: 40px;
            overflow: hidden;
            -webkit-box-orient: vertical;
            margin-bottom: 2px;
            font-family: 'Montserrat Regular';
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                font-size: 14px;
                line-height: 18px;
                height: 36px;
            }
            @media (min-width: 1100px) {
                &:hover {color: $color-text-green;}
            }
        }
        .old-price {
            position: relative;
            display: inline-block;
            margin: 0 auto;
            font-family: 'Montserrat Regular';
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #696969;
            &-wrap {
                display: flex;
                align-items: center;
                justify-content: center;
            }
            @media (max-width: 992px) {
                font-size: 14px;
                line-height: 18px;
            }
            &::before {
                position: absolute;
                top: calc(50% - 1px);
                left: 0;
                content: '';
                display: block;
                width: 100%;
                height: 1px;
                background-color: #F15C5C;
                transform: rotate(-6.52deg);
            }
        }
        .new-price {
            display: block;
            margin-bottom: 8px;
            font-family: 'Montserrat Bold';
            font-size: 25px;
            line-height: 30px;
            text-align: center;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 23px;
            }
        }
        & .add-to-cart {
            width: 100%;
            max-width: 100%;
            margin-bottom: 10px;
            text-transform: initial;
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
            & img {
                width: 100%;
                max-width: 28px;
                margin: 0 0 0 3px;
            }
        }
        .buy-in-click {
            max-width: 180px;
            margin: 0 auto;
        }
    }
    &-img {
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 100%;
        height: 195px;
        margin-bottom: 10px;
        padding: 0 3px;
        & img {
            max-width: 100%;
            max-height: 100%;
        }
    }
    &-recommandation-slider {
        width: 100%;
        max-width: 1340px;
        margin: 0 auto;
        padding: 0 115px 0;
        @media (max-width: 1260px) {
            & .owl-item {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
        @media (max-width: 1100px) {
            padding: 0 115px 40px;
        }
        @media (max-width: 992px) {
            padding: 0 75px 40px;
        }
        @media (max-width: 660px) {
            padding: 0 15px 40px;
        }
        & .product-item {margin: 0 !important;}
        & .owl-nav {
            @media (max-width: 660px) {
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 30px 0 0;
            }
            & button {
                position: absolute;
                top: calc(50% - 26px);
                display: block;
                width: 52px;
                height: 52px;
                background: url('../images/arrow-green.png') !important;
                background-size: contain !important;
                background-repeat: no-repeat !important;
                transition: 0.3s ease;
                @media (max-width: 660px) {
                    position: static;
                    width: 30px;
                    height: 30px;
                    &:not(:last-child) {margin-right: 30px;}
                }
                &:hover {
                    @media (min-width: 1100px) {
                        opacity: 0.7;
                    }
                }
                &.owl-prev {left: 15px;}
                &.owl-next {
                    right: 15px;
                    transform: rotate(180deg);
                }
                & span {display: none;}
            }
        }
    }
}