.wholesale {
    padding: 70px 0 45px;
    @media (max-width: 1100px) {
        padding: 40px 0;
    }
    & .title-small {
        margin-bottom: 50px;
        text-align: center;
    }
    &-text {margin-bottom: 50px;}
    &-benefits {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-flow: row wrap;
    }
    & .btn {
        max-width: 240px;
        margin: 0 auto;
    }
    &-bnf {
        display: block;
        width: 100%;
        max-width: 340px;
        margin: 0 2px 50px;
        @media (max-width: 500px) {
            margin: 0 0 30px;
        }
        &-img {
            display: flex;
            align-items: flex-end;
            justify-content: center;
            width: 100%;
            height: 109px;
            margin-bottom: 12px;
            @media (max-width: 500px) {
                height: auto;
            }
            & img {
                max-width: 100%;
                max-height: 100%;
            }
        }
        &-name {
            display: -webkit-box;
            height: 40px;
            -webkit-line-clamp: 2;
            overflow: hidden;
            -webkit-box-orient: vertical;
            width: 100%;
            margin-bottom: 8px;
            font-family: 'Montserrat Bold';
            font-size: 18px;
            line-height:20px;
            text-align: center;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height:22px;
            }
        }
        & p {
            display: block;
            width: 100%;
            font-family: 'Montserrat Regular';
            font-size: 18px;
            line-height:27px;
            text-align: center;
            color: $color-text-lightgray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height:22px;
            }
        }
    }
}