.footer {
    padding: 20px 0 20px;
    background-color: $color-white;
    border-top:  1px solid #DFDFDF;
    & .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width:992px) {
            flex-direction: column;
            align-items: center;
        }
    }
    &-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width:992px) {
            flex-direction: column;
            align-items: center;
            &:not(:last-child) {margin-bottom: 20px;}
        }
        &-row {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        & p {
            margin-right: 32px;
            font-family: 'Montserrat Bold';
            font-size: 16px;
            line-height: 16px;
            color: $color-gray;
            @media (max-width:992px) {
                margin: 0 0 15px;
                width: 100%;
                text-align: center;
            }
        }
        & a {
            display: flex;
            align-items: center;
            justify-content: center;
            &:not(:last-child) {
                margin-right: 20px;
                @media (max-width:600px) {
                    margin-right: 10px;
                }
            }
            & img {max-width: 100%;}
        }
    }
}