.thanks {
    padding: 170px 0 30px;
    @media (max-width: 992px) {padding: 60px 0 30px;}
    &-wrap {background-position: center;}
    & h1 {
        margin-bottom: 27px;
        font-family: 'Montserrat ExtraBold';
        font-size: 54px;
        line-height: 69px;
        color: $color-white;
        @media (max-width: 992px) {
            width: 100%;
            margin-bottom: 20px;
            font-size: 24px;
            line-height: 35px;
            text-align: center;
        }
    }
    & div {
        margin-bottom: 73px;
        font-family: 'Montserrat Medium';
        font-size: 32px;
        line-height: 39px;
        color: $color-white;
        @media (max-width: 992px) {
            width: 100%;
            margin-bottom: 35px;
            font-size: 18px;
            line-height: 24px;
            text-align: center;
        }
    }
    & p,
    & a {
        font-family: 'Montserrat Medium';
        font-size: 18px;
        line-height: 22px;
        color: $color-white;
        @media (max-width: 992px) {
            width: 100%;
            font-size: 14px;
            line-height: 16px;
            text-align: center;
        }
    }
    & a {
        color: #3D9AE2;
        text-decoration: underline;
        &:hover {text-decoration: none;}
    }
}