.benefits {
    padding: 70px 0 80px;
    &-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1100px) {
            justify-content: space-around;
            flex-flow: row wrap;
        }
        & .bnf {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            max-width: 350px;
            margin-bottom: 40px;
            padding: 20px 15px 25px 16px;
            @media (max-width: 1100px) {
                margin: 0 3px 40px;
            }
            @media (max-width: 767px) {
                margin: 20px 10px;
            }
            @media (max-width: 450px) {
                max-width: 100%;
                margin: 0 0 20px;
            }
            &-img {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 71px;
                min-width: 71px;
                margin-right: 19px;
                & img {max-width: 100%;}
            }
            &-cnt {
                width: 100%;
                & h5 {
                    margin-bottom: 7px;
                    font-family: 'Montserrat Bold';
                    font-size: 25px;
                    line-height: 30px;
                    color: $color-white;
                    @media (max-width: 992px) {
                        font-size: 18px;
                        line-height: 22px;
                    }
                }
                & p {
                    font-family: 'Montserrat Regular';
                    font-size: 18px;
                    line-height: 22px;
                    color: $color-white;
                    @media (max-width: 992px) {
                        font-size: 14px;
                        line-height: 18px;
                    }
                }
            }
            &.bnf-green {background-color: #66C58F;}
            &.bnf-red {background-color: #F54C6C;}
            &.bnf-blue {background-color: #3768B3;}
        }
    }
}