.video {
    &-main {
        padding: 70px 0 0;
        @media (max-width: 1100px) {
            padding: 40px 0 0;
        }
        &-slider {
            width: 100%;
            max-width: 1340px;
            margin: 0 auto;
            padding: 0 115px 85px;
            @media (max-width: 1100px) {
                padding: 0 115px 40px;
            }
            @media (max-width: 992px) {
                padding: 0 75px 40px;
            }
            @media (max-width: 660px) {
                padding: 0 15px 40px;
            }
            & .owl-nav {
                @media (max-width: 660px) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin: 30px 0 0;
                }
                & button {
                    position: absolute;
                    top: calc(50% - 26px);
                    display: block;
                    width: 52px;
                    height: 52px;
                    background: url('../images/arrow-green.png') !important;
                    background-size: contain !important;
                    background-repeat: no-repeat !important;
                    transition: 0.3s ease;
                    @media (max-width: 660px) {
                        position: static;
                        width: 30px;
                        height: 30px;
                        &:not(:last-child) {margin-right: 30px;}
                    }
                    &:hover {
                        @media (min-width: 1100px) {
                            opacity: 0.7;
                        }
                    }
                    &.owl-prev {left: 15px;}
                    &.owl-next {
                        right: 15px;
                        transform: rotate(180deg);
                    }
                    & span {display: none;}
                }
            }
        }
        &-item {
            width: 100%;
            max-width: 1110px;
            & iframe {
                width: 100%;
                height: 592px;
                border-radius: 20px;
                overflow: hidden;
                @media (max-width: 1100px) {
                    height: 300px;
                }
                @media (max-width: 660px) {
                    height: auto;
                    min-height: 220px;
                }
                @media (max-width: 420px) {
                    min-height: 0;
                }
            }
        }
    }
    &-section {
        padding: 70px 0 85px;
        @media (max-width: 1100px) {
            padding: 40px 0;
        }
    }
    &-list {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-flow: row wrap;
        @media (max-width: 1350px) {
            justify-content: space-around;
        }
    }
    &-item {
        display: block;
        width: 100%;
        max-width: 350px;
        margin-bottom: 55px;
        @media (max-width: 1350px) {
            margin: 0 3px 30px;
        }
        @media (max-width: 500px) {
            margin: 0 0 30px;
        }
        &-img {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-height: 348px;
            overflow: hidden;
            border-radius: 7px;
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                display: block;
                width: 100%;
                height: 100%;
                background: rgba(102, 197, 143, 0.1);
            }
            & span {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 2;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100%;
                & img {max-width: 73px;}
            }
            &-wrap {
                display: block;
                width: 100%;
                margin-bottom: 15px;
                border: 2px solid transparent;
                border-radius: 9px;
                transition: 0.3s ease;
            }
            & img {width: 100%;}
        }
        &-name {
            display: -webkit-box;
            height: 48px;
            -webkit-line-clamp: 2;
            overflow: hidden;
            -webkit-box-orient: vertical;
            font-family: 'Montserrat Medium';
            font-size: 20px;
            line-height: 24px;
            text-align: center;
            text-decoration-line: underline;
            color: $color-text-gray;
            transition: 0.3s ease;
            @media (max-width: 992px) {
                height:36px;
                font-size: 16px;
                line-height: 18px;
            }
        }
        &:hover {
            & .video-item-img-wrap {
                border: 2px solid #66C58F;
                box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);;
            }
            & .video-item-name {color: #66C58F;}
        }
    }
}