html, body {
    height: 100%;
    &.hidden {overflow: hidden;}
}

.main {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    &-content {flex: 1 0 auto;}
    & footer {flex: 0 0 auto;}
}


.wrapper {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}

.section-block {
    padding: 70px 0 45px;
    @media (max-width: 1100px) {
        padding: 40px 0;
    }
}



// text

.title {
    display: block;
    margin-bottom: 45px;
    font-family: 'Montserrat Bold';
    font-size: 40px;
    line-height: 49px;
    text-align: center;
    color: $color-text-gray;
    &-small {
        display: block;
        margin-bottom: 34px;
        font-family: 'Montserrat Bold';
        font-size: 24px;
        line-height: 29px;
        color: $color-text-gray;
    }
    &-l {
        display: block;
        margin-bottom: 45px;
        font-family: 'Montserrat Bold';
        font-size: 35px;
        line-height: 43px;
        text-align: left;
        color: $color-text-gray;  
        @media (max-width: 767px) {
            font-size: 20px;
            line-height: 26px;
            text-align: center;
            & br {display: none;}
        }
    }
    &.title-left {
        text-align: left;
        @media (max-width: 767px) {
            text-align: center;
        }
    }
    @media (max-width: 767px) {
        font-size: 26px;
        line-height: 34px;
        & br {display: none;}
    }
}

// bg

.section-bg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top left;
}

.bg-gray {background-color: #F6F6F6;}

// forms

.form-field-simple {
    display: block;
    width: 100%;
    margin-bottom: 30px;
    padding-bottom: 20px;
    font-family: 'Montserrat Thin';
    font-size: 20px;
    line-height: 20px;
    color: $color-text-gray;
    border-bottom: 1px solid $color-text-gray;
    background-color: transparent;
    transition: 0.3s ease;
    &:focus,
    &:hover {border-bottom: 1px solid $color-green;}
    &::-webkit-input-placeholder {color: $color-gray;}
    &::-moz-placeholder {color: $color-gray;}
    &:-ms-input-placeholder {color: $color-gray;}
    &:-moz-placeholder {color: $color-gray;}
}

.required-field {
    &.error {border: 1px solid red !important;}
}

.filter-input {
    display: block;
    width: 100%;
    min-width: 70px;
    max-width: 70px;
    margin: 0px 8px 10px 7px;
    padding: 0;
    font-family: 'Montserrat Medium';
    font-size: 18px;
    line-height: 18px;
    color: $color-text-gray;
    transition: 0.3s ease;
    border-bottom: 1px solid $color-gray2;
    &:focus,
    &:hover {border-bottom: 1px solid $color-green;}
    &::-webkit-input-placeholder {color: $color-text-gray;}
    &::-moz-placeholder {color: $color-text-gray;}
    &:-ms-input-placeholder {color: $color-text-gray;}
    &:-moz-placeholder {color: $color-text-gray;}
}

.form-field-gray {
    display: block;
    width: 100%;
    height: 55px;
    margin-bottom: 23px;
    padding: 0 18px;
    font-family: 'Montserrat Medium';
    font-size: 16px;
    line-height: 55px;
    color: $color-gray;
    border: 1px solid #F9F9F9;
    background-color: #F9F9F9;
    border-radius: 4px;
    transition: 0.3s ease;
    &.form-field-white {
        border: 1px solid $color-white;
        background-color: $color-white;
    }
    &:focus,
    &:hover {border: 1px solid $color-green;}
    &::-webkit-input-placeholder {color: $color-gray;}
    &::-moz-placeholder {color: $color-gray;}
    &:-ms-input-placeholder {color: $color-gray;}
    &:-moz-placeholder {color: $color-gray;}
}

textarea.form-field-gray {
    min-height: 200px;
    padding: 17px;
}

// blocks

.info-block {
    width: 100%;
    &-short {
        max-width: 775px;
        margin: 0 auto;
    }
    & p,
    & a {
        font-family: 'Montserrat Regular';
        font-size: 18px;
        line-height: 25px;
        color: $color-text-gray;
        @media (max-width: 767px) {
            font-size: 16px;
            line-height: 23px;
        }
        & b {font-family: 'Montserrat Bold';}
    }
    & > div {
        & a {display: block;}
    }
    & a {
        color: $color-green-link;
        text-decoration: underline;
        transition: 0.3s ease;
        &:hover {text-decoration: none;}
    }
    & ul {
        list-style: none;
        padding: 0;
        & li {
            position: relative;
            padding-left: 30px;
            font-family: 'Montserrat Regular';
            font-size: 18px;
            line-height: 25px;
            color: $color-text-gray;
            @media (max-width: 767px) {
                font-size: 16px;
                line-height: 23px;
            }
            &::before {
                content: '';
                position: absolute;
                top: 8px;
                left: 0;
                display: block;
                width: 16px;
                height: 12px;
                background-image: url('../images/check-true2.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}

// slider

.simple-slider {
    & .owl-nav {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: 20px 0;
        & button {
            display: block;
            width: 31px;
            height: 9px;
            background-image: url('../images/arrow-black.svg') !important;
            background-repeat: no-repeat !important;
            background-size: contain !important;
            transition: 0.3s ease;
            &:hover {
                @media (min-width: 1100px) {
                    opacity: 0.7;
                }
            }
            &.owl-next {transform: rotate(180deg);}
            &:not(:last-child) {margin-right: 30px;}
            & span {display: none;}
        }
    }
    & .owl-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        & button {
            position: relative;
            display: block;
            width: 10px;
            min-width: 10px;
            height: 10px;
            border-radius: 50%;
            border: 1px solid #898686 !important;
            background: transparent !important;
            transition: 0.3s ease;
            &:hover,
            &.active {
                & span {opacity: 1;}
            }
            &:not(:last-child) {margin-right: 10px;}
            & span {
                position: absolute;
                top: -1px;
                left: -1px;
                width: 10px;
                min-width: 10px;
                height: 10px;
                opacity: 0;
                background: $linear;
                border-radius: 50%;
                transition: 0.3s ease;
            }
        }
    }
}


// pagination


.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    & a {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 37px;
        min-width: 37px;
        height: 37px;
        font-family: 'Montserrat Regular';
        font-size: 16px;
        line-height: 16px;
        color: $color-text-gray;
        border: 1px solid #E4DFDF;
        box-sizing: border-box;
        border-radius: 4px;
        transition: 0.3s ease;
        &:not(:last-child) {margin-right: 8px;}
        &::before {
            content: '';
            position: absolute;
            top: -1px;
            left: -1px;
            z-index: 1;
            display: block;
            width: calc(100% + 1px);
            height: calc(100% + 1px);
            border-radius: 4px;
            background: $linear;
            transition: 0.3s ease;
            opacity: 0;
        }
        & span {
            position: relative;
            z-index: 2;
        }
        & svg {
            & path {transition: 0.3s ease;}
        }
        &:hover,
        &.active {
            color: $color-white;
            &::before {
                opacity: 1;
            }
            & svg {
                & path {fill: $color-white;}
            }
        }
    }
}

// choose file

.file-load {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    &-wrap {margin-bottom: 20px;}
    &-choose {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        font-family: 'Montserrat Regular';
        font-size: 13px;
        line-height: 13px;
        letter-spacing: 0.1px;
        text-decoration: underline;
        color: #2D9CDB;
        transition: 0.3s ease;
        &:hover {opacity: 0.7;}
        & img {
            max-width: 19px;
            margin-right: 5px;
        }
    }
}


// select styling

/* The container must be positioned relative: */
.custom-select {
    position: relative;
    width: 100%;
    max-width: 233px;
    height: 38px;
    font-family: 'Montserrat Regular';
    font-size: 16px;
    line-height: 16px;
    color: $color-text-gray;
    border-radius: 5.86px;
    & select {display: none; }
}

.select-selected {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color: $color-white;
    cursor: pointer;
    height: 38px;
    padding: 0 30px 0 12px;
    border: 1px solid #E4DFDF;
    border-radius: 5.86px;
    color: $color-text-gray;
    &:after {
        position: absolute;
        content: "";
        top: 11px;
        right: 13px;
        width: 16px;
        height: 16px;
        background-image: url(../images/arrow-btm.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        transition: 0.3s ease;
    }
    &.select-arrow-active:after {transform: rotate(180deg);}
}


.select-items {
    position: absolute;
    background-color: $color-white;
    top: calc(100% + 6px);
    left: 0;
    right: 0;
    z-index: 5;
    border: 1px solid $color-gray2;
    & div {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        min-height: 38px;
        padding: 5px 30px 5px 26px;
        transition: 0.3s ease;
        &:after {
            position: absolute;
            content: "";
            top: 13px;
            right: 13px;
            width: 16px;
            height: 12px;
            background-image: url(../images/select-check.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            transition: 0.3s ease;
        }
        &:not(:last-child) {border-bottom: 1px solid $color-gray2;}
    }
}


.select-hide {display: none;}

.select-items div:hover, .same-as-selected {
    background: #66C58F;
    color: $color-white;
}