.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 36px;
    padding: 0 13px;
    font-family: 'Montserrat Bold';
    font-size: 14px;
    line-height: 36px;
    text-transform: uppercase;
    color: $color-white;
    background: $linear;
    box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
    border-radius: 4px;
    transition: 0.3s ease;
    &-big {
        padding: 0 23px;
        height: 54px;
        font-family: 'Montserrat Medium';
        font-size: 18px;
        line-height: 54px;
        border-radius: 7px;
        @media (max-width: 767px) {
            padding: 0 13px;
            height: 36px;
            font-family: 'Montserrat Bold';
            font-size: 14px;
            line-height: 36px;
            border-radius: 4px;
            &.btn-border::before {border-radius: 4px !important;}
        }
        &.btn-border::before {border-radius: 7px !important;}
    }
    &.btn-border {
        position: relative;
        box-shadow: none;
        border: 1px solid #66C58F;
        background: transparent;
        transition: 0s ease;
        // &::before {
        //     content: '';
        //     position: absolute;
        //     top: 1px;
        //     left: 1px;
        //     z-index: 1;
        //     display: block;
        //     width: calc(100% - 2px);
        //     height: calc(100% - 2px);
        //     background: $color-white;
        //     border-radius: 4px;
        //     transition: 0.3s ease;
        // }
        // &.btn-border-gray::before {
        //     background: #F9F9F9;
        // }
        &.btn-border-gray {
            background: #F9F9F9;
        }
        & span {
            position: relative;
            z-index: 2;
            color: $color-text-green;
            transition: 0.3s ease;
        }
        &:hover {
            @media (min-width: 1100px) {
                box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
                background: linear-gradient(126.52deg, #67D293 -18.05%, #63A483 108.71%) !important; 
                &::before {background: transparent !important;}
                & span {color: $color-white;}
            }
        }
    }
    & img {
        max-width: 14px;
        margin-right: 5px;
    }
    &:hover {
        @media (min-width: 1100px) {
            box-shadow: none;
            color: $color-white;
        }
    }
}