.cart {
    &-trigger {
        display: block;
        position: relative;
        &.empty span {display: none;}
        &-wrap {
            position: relative;
        }
        & span {
            position: absolute;
            top: -3px;
            right: -14px;
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 20px;
            width: 20px;
            height: 20px;
            font-family: 'Montserrat Bold';
            font-size: 14px;
            line-height: 14px;
            color: $color-white;
            background: #F15C5C;
            border-radius: 50%;
            border: 2px solid $color-white
        }
    }
    &-small {
        width: 100%;
        padding: 17px 15px;
        background-color: $color-white;
        box-shadow: 0px 11px 50px rgba(0, 0, 0, 0.16);
        &-wrap {
            position: absolute;
            top: 100%;
            left: 50%;
            z-index: 8;
            display: none;
            transform: translateX(-50%);
            padding-top: 19px;
            min-width: 300px;
            @media (max-width: 992px) {
                left: auto;
                right: -94px;
                transform: translateX(0);
            }
            @media (max-width: 600px) {
                right: -70px;
                min-width: 290px;
            }
            &::before {
                content: '';
                position: absolute;
                top: 1px;
                left: calc(50% - 21px);
                z-index: 11;
                display: block;
                width: 42px;
                height: 19px;
                clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
                background-color: $color-white;
                @media (max-width: 992px) {
                    left: auto;
                    right: 88px;
                }
                @media (max-width: 600px) {
                    right: 64px;
                }
            }
        }
        &-buttons {
            & .btn {
                max-width: 100%;
                &:not(:last-child) {margin-bottom: 17px;}
            }
        }
        &-row {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-bottom: 17px;
            padding: 0 0 16px;
            border-bottom: 1px solid $color-gray2;
            &-img {
                display: flex;
                align-items: center;
                justify-content: center;  
                width: 100%;
                max-width: 80px;
                min-width: 80px;
                height: 80px;
                margin-right: 10px;
                padding: 3px;
                border: 1px solid #E4DFDF;
                border-radius: 5.86px;
                & img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
            &-cnt {
                position: relative;
                width: 100%;
                max-width: 170px;
                padding-right: 27px;
                & .delete-item {
                    position: absolute;
                    top: calc(50% - 9.5px);
                    right: 0;
                }
            }
            &-name {
                font-family: 'Montserrat Bold';
                font-size: 14px;
                line-height: 17px;
                height: 34px;
                margin-bottom: 25px;
                color: $color-text-gray;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                overflow: hidden;
                -webkit-box-orient: vertical;
                transition: 0.3s ease;
                @media (max-width: 600px) {
                    font-size: 12px;
                    line-height: 15px;
                    height: 30px;
                }
                &:hover {
                    @media (min-width: 1100px) {
                        color: $color-green;
                    }
                }
            }
            &-info {
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
            &-price {
                font-family: 'Montserrat Bold';
                font-size: 14px;
                line-height: 14px;
                color: #66C58F;
            }
            &-amount {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                font-family: 'Montserrat Regular';
                font-size: 15px;
                line-height: 15px;
                color: $color-text-gray;
                & img {
                    max-width: 17px;
                    margin-right: 5px;
                }
            }
        }
    }
    &-list { 
        padding-bottom: 75px;
        &.delete-items-block {
            & p.no-items {
                display: none;
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 22px;
                text-align: center;
                color: $color-text-gray;
                & .btn {
                    max-width: 300px;
                    margin: 30px auto 0;
                }
            }
            &.empty {
                & p.no-items {display: block;}
                & .cart-row,
                & .cart-next {display: none;}
            }
        }
    }
    &-row {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 100%;
        padding: 38px 65px 38px 0;
        border-bottom: 1px solid $color-gray2;
        &:not(.cart-row-info) {
            @media (max-width: 767px) {
                flex-direction: column;
                align-items: center;
                max-width: 350px;
                margin: 0 auto 30px;
                padding: 20px 15px;
                border-radius: 4px;
                border: 1px solid $color-text-green;
            }
        }
        & .delete-item {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 40px;
            @media (max-width: 767px) {
                position: static;
                transform: none;
                margin: 0 auto;
            }
        }
        &-product {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            max-width: 420px;
            min-width: 420px;
            padding-right: 10px;
            @media (max-width: 1100px) {
                max-width: 300px;
                min-width: 300px;
            }
            @media (max-width: 767px) {
                flex-direction: column;
                max-width: 100%;
                min-width: 0;
                padding: 0 0 15px;
            }
            &-img {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                max-width: 80px;
                min-width: 80px;
                height: 80px;
                margin-right: 60px;
                padding: 3px;
                border: 1px solid #E4DFDF;
                border-radius: 5.86px;
                @media (max-width: 1100px) {
                    margin-right: 30px;
                }
                @media (max-width: 767px)  {
                    margin: 0 auto 15px;
                }
                & img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
            & a {
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 22px;
                color: $color-text-gray;
                transition: 0.3s ease;
                @media (max-width: 992px) {
                    font-size: 16px;
                    line-height: 20px;
                }
                @media (max-width: 767px) {text-align: center;}
                &:hover {color: $color-text-green;}
            }
        }
        &-price {
            width: 100%;
            max-width: 100px;
            min-width: 100px;
            @media (max-width: 767px) {display: none;}
            & p {
                width: 100%;
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 22px;
                text-align: center;
                color: $color-text-gray;
                @media (max-width: 992px) {
                    font-size: 16px;
                    line-height: 20px;
                }
            }
        }
        &-count {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            max-width: 310px;
            min-width: 310px;
            @media (max-width: 1100px) {
                max-width: 150px;
                min-width: 150px;
                padding: 0 15px;
            }
            @media (max-width: 767px) {
                max-width: 100%;
                min-width: 0;
                padding: 0 0 15px;
            }
            & .buy-item-count-wrap,
            & .buy-item-count {margin: 0;}
        }
        &-sum {
            width: 100%;
            max-width: 100px;
            min-width: 100px;
            @media (max-width: 767px) {
                max-width: 100%;
                min-width: 0;
                padding: 0 0 15px;
            }
            & p {
                width: 100%;
                font-family: 'Montserrat Regular';
                font-size: 18px;
                line-height: 22px;
                text-align: center;
                color: $color-text-gray;
                @media (max-width: 992px) {
                    font-size: 16px;
                    line-height: 20px;
                }
            }
        }
        &-info {
            padding-right: 0;
            justify-content: space-between;
            @media (max-width: 767px) {
                flex-direction: column;
                align-items: center;
                padding: 20px 0;
                border: none;
                & .btn {order: 2;}
                & .checkout-info-row {
                    order: 1;
                    margin: 0 auto 15px !important;
                }
            }
        }
        & .checkout-info-row-price {
            margin-bottom: 0;
            padding: 0;
            border: none;
        }
    }
    &-next {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-top: 38px;
        @media (max-width: 992px) {padding-top: 20px; }
        @media (max-width: 767px) {justify-content: center; }
        & .btn {max-width: 350px;}
    }
    &-head {
        min-height: 57px;
        padding: 19px 0;
        background: $linear;
        border-radius: 5px;
        @media (max-width: 767px) {
            display: none;
        }
        & div {
            font-family: 'Montserrat Bold';
            font-size: 18px;
            line-height: 18px;
            text-align: center;
            color: #FFFFFF;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 16px;
            }
            &.cart-row-product {padding-left: 44px;}
        }
    }
}

.delete-item {
    width: 19px;
    min-width: 19px;
    height: 19px;
}

.delete-items-block {
    .simple-text {
        display: none;
        margin-bottom: 20px;
        font-family: 'Montserrat Bold';
        font-size: 16px;
        line-height: 16px;
        text-align: center;
        color: $color-text-gray;
        & span {
            display: block;
            margin-top: 15px;
            font-family: 'Montserrat Regular';
            font-size: 13px;
            line-height: 16px;
        }
    }
    &.empty {
        & .simple-text {display: block;}
        & .cart-small-buttons {display: none;}
    }
}