.reviews {
    padding: 70px 0 85px;
    @media (max-width: 1100px) {
        padding: 40px 0;
    }
    &-main {
        padding: 70px 0 85px;
        @media (max-width: 1100px) {
            padding: 40px 0;
        }
        & .title {margin-bottom: 30px;}
    }
    &-form {
        margin-bottom: 50px;
        & form {
            display: none;
            width: 100%;
            max-width: 540px;
            margin: 50px auto 0;
            .file-load-wrap {
                display: flex;
                align-items: center;
                justify-content: center;
            }
            & .btn {margin: 0 auto;}
        }
        &.active {& form {display: block;}}
        & .show-rev-form {
            margin: 0 auto;
            max-width: 240px;
        }
    }
}

.rev {
    width: 100%;
    margin-bottom: 20px;
    &:not(:first-child) {
        padding-left: 65px;
        @media (max-width: 992px) {
            padding-left: 0;
        }
    }
    &-block {
        width: 100%;
        margin-bottom: 50px;
        padding: 40px 15px 15px 95px;
        background: #FFFFFF;
        box-shadow: 0px 11px 32px rgba(0, 0, 0, 0.16);
        border-radius: 10px;
        @media (max-width: 992px) {
            padding: 40px 15px 15px 15px;
        }
        & form {
            margin: 50px 0 30px;
            .file-load-wrap {justify-content: flex-start;}
            & .btn {margin: 0;}
        }
    }
    &-head {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 20px;
        &-cnt {
            & p {
                display: block;
                margin-bottom: 7px;
                font-family: 'Montserrat Bold';
                font-size: 22px;
                line-height: 27px;
                color: $color-text-gray;
                @media (max-width: 992px) {
                    font-size: 18px;
                    line-height: 22px;
                }
            }
            & span {
                display: block;
                font-family: 'Montserrat Bold';
                font-size: 16px;
                line-height: 16px;
                color: #2C8A52;
                background: linear-gradient(126.52deg, #67D293 -18.05%, #63A483 108.71%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                @media (max-width: 992px) {
                    font-size: 14px;
                    line-height: 14px;
                }
            }
        }
    }
    &-cnt {
        &-images {
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-flow: row wrap;
            margin-bottom: 30px;
            & a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                min-width: 50px;
                height: 50px;
                margin-right: 10px;
                border: 1px solid #E4DFDF;
                box-sizing: border-box;
                border-radius: 5.86px;
                & img {
                    max-width: 100%;
                    max-height: 100%;
                }
            }
        }
        & p {
            display: block;
            margin-bottom: 10px;
            font-family: 'Montserrat Medium';
            font-size: 18px;
            line-height: 22px;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
        & .show-rev-form {
            display: block;
            margin: 0;
            max-width: 100%;
            font-family: 'Montserrat Regular';
            font-size: 15px;
            line-height: 15px;
            color: $color-text-lightgray;
            transition: 0.3s ease;
            &:hover {color: $color-text-green;}
        }
    }
    &-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 70px;
        min-width: 70px;
        height: 70px;
        margin-right: 20px;
        border-radius: 50%;
        overflow: hidden;
        & img {
            max-width: 100%;
            max-height: 100%;
        }
    }
}

.rev-slider {
    & .owl-dots {margin: 20px 0 0;}
    &-wrap {
        width: 100%;
        max-width: 532px;
        margin: 0 auto;
    }
    &-item {
        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100px;
            height: 100px;
            margin: 0 auto 30px;
            border: 3px solid transparent;
            border-radius: 50%;
            transition: 0.3s ease;
            border: 3px solid #64AC86;
            & img {width: 100%;}
        }
        & p {
            margin-bottom: 33px;
            font-family: 'Montserrat Medium';
            font-size: 18px;
            line-height: 22px;
            text-align: center;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
        & p {
            margin-bottom: 33px;
            font-family: 'Montserrat Medium';
            font-size: 18px;
            line-height: 22px;
            text-align: center;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 16px;
                line-height: 20px;
            }
        }
        & .rev-name {
            margin-bottom: 17px;
            font-family: 'Montserrat Bold';
            font-size: 22px;
            line-height: 27px;
            text-align: center;
            color: $color-text-gray;
            @media (max-width: 992px) {
                font-size: 18px;
                line-height: 22px;
            }
        }
        & .rev-date {
            font-family: 'Montserrat Bold';
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #2C8A52;
            background: linear-gradient(126.52deg, #67D293 -18.05%, #63A483 108.71%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
}