.surprise {
    padding: 45px 0 72px;
    background-position: center;
    @media (max-width: 600px)  {
        background: #F1F2ED !important;
    }
    & .wrapper {max-width: 600px;}
    &-title {
        margin-bottom: 20px;
        font-family: 'Montserrat ExtraBold';
        font-size: 51px;
        line-height: 62px;
        text-align: center;
        color: $color-green;
        background: $linear;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: linear-gradient(126.52deg, #67D293 -18.05%, #63A483 108.71%);
        @media (max-width: 992px)  {
            margin-bottom: 15px;
            font-size: 31px;
            line-height: 42px;
        }
    }
    &-subtitle {
        margin-bottom: 35px;
        font-family: 'Montserrat Medium';
        font-size: 28px;
        line-height: 34px;
        text-align: center;
        color: $color-text-gray;
        @media (max-width: 992px)  {
            margin-bottom: 25px;
            font-size: 18px;
            line-height: 24px;
        }
    }
    & form {
        display: block;
        width: 100%;
        max-width: 395px;
        margin: 0 auto;
        & .btn {margin: 48px auto 0;}
    }
}