.popup {
    position: relative;
    width: 100%;
    &-thanks {
        max-width: 732px;
        & .popup-cnt-wrap {
            padding: 64px 15px;
            border-radius: 10px;
            & .popup-img {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                margin-bottom: 30px;
                & img { max-width: 115px;}
            }
            & h5 {
                font-family: 'Montserrat Regular';
                font-weight: 300;
                font-size: 26px;
                line-height: 35px;
                text-align: center;
                color: $color-text-darkgray;
            }
        }
        & .popup-close {
            top: 10px;
            right: 10px;
        }
    }
    & .form-title {
        margin-bottom: 22px;
        font-family: 'Montserrat Regular';
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: #423E3C;
        @media (max-width: 767px) {
            font-size: 16px;
            line-height: 20px;
        }
    }
    &-wrapper {
        position: fixed;
        z-index: 25;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        display: none;
        transition: 0.4s ease;
        &.active {display: block;}
        &#polyticsPopup {z-index: 30;}
        &.modal .popup {
            width: 100%;
            max-width: 792px;
        }
        &.buy-modal .popup {
            max-width: 1000px;
            & .popup-close {
                top: 0;
                right: 0px !important;
            }
        }
    }
    &-layout {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: 6;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        padding: 20px;
        overflow-y: scroll;
        background: rgba(0, 0, 0, 0.6);
        &::-webkit-scrollbar {width: 0px;}
        &::-webkit-scrollbar-track {box-shadow: inset 0 0 0px $color-white;}
        &::-webkit-scrollbar-thumb {background-color: transparent;}
    }
    &-text {
        width: 100%;
        max-width: 526px;
        margin: 0 auto 16px;
        font-family: 'Montserrat Regular';
        font-size: 18px;
        line-height: 21px;
        color: #423E3C;
        & p:not(:last-child) {margin-bottom: 21px;}
    }
    & .btn {margin: 0 auto;}
    &-close {
        position: absolute;
        cursor: pointer;
        z-index: 2;
        top: 22px;
        right: 68px;
        width: 100%;
        max-width: 40px;
        & img {width: 100%;}
    }
    &-head {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 84px;
        padding: 5px 40px;
        background: $linear;
        box-shadow: 0px 7px 50px rgba(0, 0, 0, 0.12);
        border-radius: 10px 10px 0 0;
        & span {
            font-family: 'Montserrat Medium';
            font-size: 25px;
            line-height: 25px;
            text-align: center;
            letter-spacing: -0.3px;
            color: $color-white;
            @media (max-width: 767px) {
                font-size: 18px;
                line-height: 18px;
            }
        }
    }
    &-cnt {
        &-flex {
            width: 100%;
            & .title {
                margin-bottom: 25px;
                text-align: left;
                @media (max-width: 767px) {
                    margin-bottom: 30px;
                    text-align: center;
                }
            }
            &-wrap {
                padding: 38px 22px !important;
                background-color: $color-white !important;
                border-radius: 10px !important;
                @media (max-width: 767px) {padding: 38px 15px !important;}
            }
            &-list {
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                @media (max-width: 767px) {flex-direction: column;}
            }
            & .hidden-input {display: none;}
            & .form-btm {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                @media (max-width: 767px) {display: none;}
                & .btn {margin-bottom: 25px;}
            }
            &-info {
                width: 100%;
                max-width: 440px;
                margin-right: 15px;
                @media (max-width: 767px) {
                    max-width: 100%;
                    margin: 0 0 30px;
                }
            }
            & .popup-cnt-form {
                width: 100%;
                max-width: 410px;
                @media (max-width: 767px) {max-width: 100%;}
                & .form-row {margin-bottom: 11px;}
                & .pay-method {
                    max-width: 410px;
                    margin: 0 auto;
                    padding-top: 20px;
                    @media (max-width: 767px) {margin-bottom: 20px;}
                    & p {
                        margin-bottom: 20px;
                        font-family: 'Montserrat Bold';
                        font-size: 16px;
                        line-height: 16px;
                        color: $color-text-gray;
                        @media (max-width: 767px) {
                            font-size: 14px;
                            line-height: 14px;
                        }
                    }
                    & .custom-select {
                        width: 100%;
                        max-width: 100%;
                    }
                }
                & .form-btm {
                    display: none;
                    @media (max-width: 767px) {display: flex;}
                }
            }
        }
        &-wrap {
            padding: 32px 15px;
            background-color: #F8F9FA;
            border-radius: 0 0 10px 10px;
        }
        &-text {
            padding: 24px 15px 28px;
            font-family: 'Montserrat Light';
            font-size: 16px;
            line-height: 21px;
            color: #000000;
            &.no-border {border: none !important;}
            & p:not(:last-child) {margin-bottom: 20px;}
        }
        &-form {
            width: 100%;
            max-width: 410px;
            margin: 0 auto;
        }
    }
}


.buy-item {
    &-img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 245px;
        max-height: 245px;
        margin-bottom: 25px;
        & img {
            max-width: 100%;
            max-height: 100%;
        }
    }
    &-price {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 15px;
        font-family: 'Montserrat Bold';
        font-size: 40px;
        line-height: 40px;
        color: $color-text-green;
        @media (max-width: 767px) {
            font-size: 20px;
            line-height: 20px;
        }
    }
    &-count {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 10px;
        &-wrap {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin-bottom: 30px;
            & p {
                font-family: 'Montserrat Regular';
                font-size: 15px;
                line-height: 15px;
                color: #848B93;
            }
        }
        & span {
            user-select: none;
            cursor: pointer;
            font-family: 'Montserrat Thin';
            font-size: 40px;
            line-height: 40px;
            min-width: 20px;
            text-align: center;
            color: #66C58F;
            transition: 0.3s ease;
            &:hover {opacity: 0.8;}
        }
        & input {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 37px;
            max-width: 37px;
            height: 35px;
            margin: 0 15px;
            font-family: 'Montserrat Bold';
            font-size: 25px;
            line-height: 25px;
            text-align: center;
            color: $color-text-gray;
            background: $color-white;
            border: 1px solid #B7BDC3;
            border-radius: 3.25581px;
        }
    }
}



.form-row {
	position: relative;
	display: block;
	width: 100%;
	max-width: 410px;
    margin: 0 auto;
    &:not(:last-child) {margin-bottom: 11px;}
    & > span {
        position: absolute;
        top: calc(50% - 7.5px);
        left: 17px;
    }
    & span {
        font-family: 'Montserrat Regular';
        font-size: 15px;
        line-height: 15px;
        color: #848B93;
        & span {
            font-size: 21px;
            color: #DB1127;
        }
    }
    &.active span:not(.square-info) {display: none;}
    & input {
        height: 68px;
        margin-bottom: 0 !important;
        padding-left: 17px;
        font-family: 'Montserrat Regular';
        font-size: 15px;
        line-height: 68px;
        color: $color-text-lightgray;
    }
    & input:focus + span {display: none;}
}
	
.form-field {
    display: block;
    width: 100%;
    max-width: 410px;
    height: 60px;
    padding: 0 17px 0;
    margin: 0 auto;
    font-family: 'Montserrat Regular';
    font-size: 15px;
    line-height: 60px;
    color: $color-text-lightgray;
    background: #ffffff;
    border: 1px solid #B7BDC3;
    border-radius: 4px;
    transition: 0.3s ease;
    &::-webkit-input-placeholder {color: $color-text-lightgray;}
    &::-moz-placeholder {color: $color-text-lightgray;}
    &:-ms-input-placeholder {color: $color-text-lightgray;}
    &:-moz-placeholder {color: $color-text-lightgray;}
}

.popup .tel-trigger {margin-bottom: 11px;}

textarea.form-field  {
	margin-bottom: 22px;
	padding: 12px 17px;
	height: 136px;
}
	
.square-info{
	position: absolute;
	top: 0 !important;
	right: 2px !important;
	left: auto !important;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 62px;
	height: 100%;
	background: $color-white;
	border: 2px solid #EDEDED;
	border-right: 2px solid transparent;
	box-sizing: border-box;
	box-shadow: 0px 4px 26px rgba(0, 0, 0, 0.05);
	border-radius: 4px;
	color: #423E3C;
}

.form-row-square input {padding-right: 70px;}

.checkbox-item {
	cursor: pointer;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	padding: 0;
	margin-bottom: 20px;
}

.checkbox-item input {display: none;}

.checkbox-item .checker {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 18px;
	min-width: 18px;
	height: 18px;
	margin-top: 3px;
	margin-right: 8px;
	border: 2px solid #212121;
	border-radius: 3px;
	transition: 0.3s ease;
}

.checker-naming {
    margin-right: 7px;
    font-family: 'Montserrat Regular';
    font-size: 16px;
    line-height: 16px;
    color: #696969;
}

.checkbox-item .checker img {
	position: relative;
	z-index: 2;
	width: 10px;
	height: 7px;
	opacity: 0;
}

.checkbox-item .checker::before {
	content: '';
	position: absolute;
	z-index: 1;
	top: 0px;
	left: 0px;
	display: block;
	width: 100%;
	height: 100%;
	background: #212121;
	transition: 0.3s ease;
	opacity: 0;
}

.modal .popup-close,
.work-popup .popup-close {right: 15px !important;}

.checker-name {
    display: block;
    position: relative;
    margin-bottom: 20px;
    padding-left: 28px;
    font-family: 'Montserrat Medium';
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: $color-gray;
    @media (max-width: 1100px) {
        font-size: 14px;
        line-height: 16px;
    }
    @media (max-width: 767px) {
        font-size: 11px;
        line-height: 13px;
    }
    &::before {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        display: block;
        width: 15px;
        height: 15px;
        background-color: #F9F9F9;
    }
    &::after {
        content: '';
        position: absolute;
        top: -2px;
        left: 2px;
        display: block;
        width: 18px;
        height: 18px;
        background-image: url('../images/check-true.svg');
        background-repeat: no-repeat;
        background-size: contain;
        transition: 0.3s ease;
        opacity: 0;
    }
}



.popup .checker-name {
    padding-left: 0;
    font-family: 'Montserrat Regular';
    font-size: 13px;
    line-height: 16px;
    & a {
        color: #2D9CDB;
        text-decoration: underline;
        &:hover {text-decoration: none;}
    }
    &::after,
    &::before {display: none;}
}

.checkbox-item  .checker-name a,
.checkbox-item  .checker-name span {
    color: #2C8A52;
    background: linear-gradient(126.52deg, #67D293 -18.05%, #63A483 108.71%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
	text-decoration: underline;
    @media (max-width: 767px) {display: block;}
}

.checkbox-item  .checker-name a:hover,
.checkbox-item  .checker-name span:hover {text-decoration: none;}

.checkbox-item input:checked + .checker img,
.checkbox-item input:checked + .checker:before,
.checkbox-item input:checked + .checker-name::after {opacity: 1;}
	
.form-field-check.required.error {border: 1px solid red;}